import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.scss']
})
export class InventoryComponent implements OnInit {
  visible_add: boolean = false; // biến này để show hoặc hide drawer
  name_inventory: string = '';
  inventory_times: any = [];
  isVisible: boolean = false;
  id_choose: any = '';
  visible_edit: boolean = false;
  open_status: boolean = false;
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private nzModalService:NzModalService
  ) { }

  ngOnInit(): void {
    this.getData();
  }
  /**
   * get danh sách đợt kiểm
   */
  getData() {
    this.vhQueryCafe.getInventorytimes().then((localInventorytimes: any) => {
      this.open_status = true;
      this.inventory_times = localInventorytimes.filter(item => item.id_branch == this.vhQueryCafe.getDefaultBranch()._id).reverse();
     console.log(this.inventory_times);
    })

  }
  goBack() {
    this.router.navigate(["/cafe/dashboard/management/stock"]);
  }
  /**
   * đóng drawer tạo đợt kiểm
   */
  closeAdd() {
    this.visible_add = false;
    this.visible_edit = false;
    this.name_inventory = "";
  }
  /**
   * thêm đợt kiểm vào DB
   */
  addInventory() {
    if (this.name_inventory.trim().length) {
      let data = {
        name: this.name_inventory,
        date: new Date().toISOString(),
        id_branch: this.vhQueryCafe.getDefaultBranch()._id
      }
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQueryCafe.addInventorytime(data).then(res => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(2000, this.languageService.translate("Inventory times has been created successfully"), "success-toast");
            this.visible_add = false;
            this.name_inventory = "";
            this.inventory_times.push(res)
          })
        })
      })
    } else {
      this.vhComponent.showToast(2000, this.languageService.translate('Name is required'), 'alert-toast')
    }
  }
  /**
   * mở drawer tạo đợt kiểm
   */
  showDrawer() {
    this.visible_add = true;
    this.name_inventory = "";
  }
  handleCancel() {
    this.isVisible = false;
  }
  /**
   * mở modal xóa đợt kiểm
   */
  showModal(item) {
    this.isVisible = true;
    this.id_choose = item._id;
  }
  /**
   * mở modal cập nhật tên đợt kiểm
   */
  showModalEdit(item) {
    this.name_inventory = item.name;
    this.visible_edit = true;
    this.id_choose = item;
  }
  /**
   * cập nhật tên đợt kiểm lên DB
   */
  editInventory() {
    if (this.name_inventory.trim().length) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.vhQueryCafe.updateInventorytime(this.id_choose._id, { name: this.name_inventory }).then((res) => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(2000, this.languageService.translate("Updated successfully"), "success-toast")
            this.visible_edit = false;
            this.id_choose.name = this.name_inventory
            // this.name_inventory = "";
            // this.getData();

          })
        })
      })
    } else {
      this.vhComponent.showToast(2000, this.languageService.translate('Name is required !'), 'alert-toast');
    }
  }
  /**
   * xóa đợt kiểm khỏi DB
   */
  deleteInventoryTimes() {
    if(this.inventory_times.balanced){
      this.nzModalService.confirm({
        nzTitle : this.languageService.translate("Đợt kiểm này đã được cân bằng! Không thể xóa")
      })
    }
    else
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQueryCafe.deleteInventorytime(this.id_choose).then((res: any) => {
        this.vhComponent.hideLoading(0).then(() => {
          if (res) {
            this.vhComponent.showToast(2000, this.languageService.translate("Inventory times has been cancelled successfully"), "success-toast")
            this.isVisible = false;
            this.inventory_times = this.inventory_times.filter(item => item._id != this.id_choose)
            // this.getData()
          }
          else {
            this.vhComponent.showToast(2000, this.languageService.translate("Can't delete this inventory times"), 'alert-toast')
          }
        })
      })
    })
  }
  gotoDetail(item) {
    this.router.navigate(["/cafe/dashboard/management/stock/inventory/detail"], { state: { inventory_times: item } })
  }
}
